
    import {
        defineComponent,
        reactive,
        onMounted
    } from "vue";
    import {
        apiSystemInfo
    } from "@/api/setting"
    export default defineComponent({
        setup() {

            const info = reactive({
                server: [], // 服务器信息
                env: [], // PHP环境要求
                auth: [], // 目录权限
            })


            const getSystemInfo = () => {
                apiSystemInfo()
                    .then((res: any) => {
                        console.log('res', res)
                        info.server = res.server
                        info.env = res.env
                        info.auth = res.auth
                    })
            }

            onMounted(() => {
                getSystemInfo()
            })

            return {
                info,
                getSystemInfo
            }
        },
    })
